body {
  position: relative;
  background: #f8f8f8;
  min-height: 650px;
  height: 100vh;
  //font-size: 16px;
  .container#registerPage {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;

    transform: translateY(-50%);

    padding: 15px;

    border-radius: 15px;
    box-shadow: 0 15px 16.83px 0.17px rgb(0 0 0 / 5%);
    background: #fff;

    #formRegister {
      .title {
        text-align: center;
        font-size: 40px;
        font-weight: bold;
      }
      .formContent{
        .form-group{
          margin-bottom: 1.7rem;
          svg{
            margin-right: 10px;
          }
          label{
            margin-bottom: 2px;
          }
          &.action{
            margin-top: 15px;
            text-align: center;
          }
        }
      }
    }

    #registerImage{
      align-self: center;
    }
  }

}
